  text-align: left;
  width: 220px;
}
.shopcart-wrapper .cart-select .cart-img {
  width: 100px;
  height: 100px;
  margin-left: 35px;
  vertical-align: middle;
}
.shopcart-wrapper .cart-checkbox {
  width: 18px;
  height: 18px;
  border: 1px solid #bdbdbd;
  display: inline-block;
  background-color: #fff;
  border-radius: 2px;
  font-size: 10px;
  vertical-align: middle;
}
.shopcart-wrapper .cart-name {
  width: 270px;
  text-align: left;
}
.shopcart-wrapper .cart-name .cart-title {
  display: inline-block;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.shopcart-wrapper .cart-name .cart-desc {
  color: #999;
}
.shopcart-wrapper .cart-price {
  width: 220px;
  text-align: center;
}
.shopcart-wrapper .cart-num {
  width: 200px;
  text-align: center;
}
.shopcart-wrapper .cart-total {
  width: 160px;
  text-align: center;
}
.shopcart-wrapper .cart-operate {
  width: 170px;
  padding-right: 40px;
  text-align: right;
}
.shopcart-wrapper .shopcart-list {
  background-color: #fff;
  margin-bottom: 10px;
}
.shopcart-wrapper .cart-product {
  height: 150px;
  border-top: 1px solid #efefef;
}
.shopcart-wrapper .cart-product:last-child {
  border-bottom: 1px solid #efefef;
}
.shopcart-wrapper .cart-input {
  width: 125px;
  height: 38px;
  border: 1px solid #efefef;
  border-radius: 4px;
  margin: 0 auto;
}
.shopcart-wrapper .cart-input button {
  width: 30px;
  height: 30px;
  text-align: center;
  background: transparent;
  border: none;
  font-size: 20px;
  cursor: pointer;
}
.shopcart-wrapper .cart-input input {
  width: 58px;
  height: 36px;
  border: none;
  border-left: 1px solid #efefef;
  border-right: 1px solid #efefef;
  text-align: center;
}
.shopcart-wrapper .shopcart-footer {
  height: 70px;
  line-height: 70px;
  background-color: #fff;
  padding: 0 45px;
  color: #666;
}
.shopcart-wrapper .shopcart-footer .footer-remove {
  color: #999;
  margin: 0 30px 0 20px;
}
.shopcart-wrapper .shopcart-footer .footer-number {
  font-weight: bold;
  margin: 0 5px;
}
.shopcart-wrapper .shopcart-footer .gray {
  color: #333;
}
.shopcart-wrapper .shopcart-footer .blue {
  color: #00c3f5;
}
.shopcart-wrapper .shopcart-footer .red {
  color: #e02b41;
}
.shopcart-wrapper .shopcart-footer .footer-total {
  font-size: 20px;
  margin-right: 35px;
}
.shopcart-empty {
  width: 1240px;
  margin: 10px auto;
  background-color: #fff;
  display: table;
  height: 360px;
}
.shopcart-empty .empty-content {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}
.shopcart-empty .empty-left {
  display: inline-block;
  width: 100px;
  height: 114px;
  background: url(data:image/png;base64,) no-repeat;
  margin-right: 30px;
  vertical-align: middle;
}
.shopcart-empty .empty-right {
  display: inline-block;
  vertical-align: middle;
  text-align: left;
}
.shopcart-empty .empty-desc {
  color: #666;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 15px;
}
